.header {
 height: 50px;
  width: 100%;
  top: 0;
  left: 0;
  position: relative;
}

.header:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #fff;
  z-index: 5;
  box-shadow: 0px 2px 3px rgb(177, 177, 177);
  
}

.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
  justify-content: flex-start;
          
}

.header__logo {
  color: #202020;
  z-index: 6;
  flex: 0 0 auto;
  display: flex;
  margin-left: 10px;
  margin-right: 10px;
  font-weight: 600;
}
.header__logo span{
  color: red;
  font-size: 20px;
}
.header__user {
  
  overflow: hidden;
	line-height: 20px;
  z-index: 6;
  display: flex;
  flex: 1 0 auto;
  justify-content: flex-end;
  margin-right: 10px;
 
}
.header__user a{
  flex: 0 1 auto;
  max-width: 100ch;
  font-size: 14px;
  color: #2775FF;
  }
.header__user a p{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header__burger {
  display: none;
  flex: 0 0 20px;
  margin-right: 15px;
}

.header__menu {
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.header__list {
  display: flex;
  position: relative;
  justify-content: center;
  z-index: 5;
  padding-left: 0px;
}

.header__list li {
  list-style: none;
  margin-left: 20px;
}
.header__list li a{
  font-size: 15px;
  color: #757575;
  
}
.header__list li a:hover{
  transition: all 0.3s;
  color: #2e2e2e;
}

.header__link {

  font-size: 18px;
  text-decoration: none;
  outline: none;
}

.overlay {
  height: calc(100vh);
  position: relative;
  background-color: black;
  opacity: 0;
  position: fixed;
  left: 0;
  z-index: -2;
  width: 100%;
}

.overlay_active {
  z-index: 2;
  opacity: 0.7;
}

@media (max-width: 767px) {
  .header__burger {
    display: block;
    position: relative;
    width: 25px;
    height: 20px;
    z-index: 5;
  }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger span {
    position: absolute;
    background-color: black;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before {
    content: '';
    background-color: black;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    top: 1px;
    transition: all 0.3s;
  }
  .header__burger:after {
    content: '';
    background-color: black;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 1px;
    transition: all 0.3s;
  }
  .header__menu {
    position: fixed;
    top: calc(-100%);
    left: 0;
    width: 100%;
    height: auto;
    background-color: #fff;
    padding: 70px 0px 0px 0px;
    z-index: 3;
    transition: all 0.7s;
  }
  .header__menu.active {
    top: 0;
    transition: all 0.7s;
   
  }
  .header__list {
    display: block;
    margin-top: 0px;
    margin-bottom: 0px;
  }
  .header__list li {
    margin-bottom: 20px;
  }
  .header__body {
    justify-content: flex-end;
  }
  .header__user a{
    font-size: 14px;
  }
}

