.checkbox {
  height: 29;
  margin-bottom: 15px;
}

.checkbox p {
  flex: 1 0;
  padding-top: 4px;
  height: 23;
  color: #383838;
  font-size: 16px;
  margin-left: 10px;
  margin: 0px;
}

.checkbox p:hover {
  color: black;
}

.checkbox input[type="checkbox"] {
  opacity: 0;
}

.checkbox label {
  display: flex;
  position: relative;
  cursor: pointer;
  padding-left: 32px;
}

.checkbox label::before,
.checkbox label::after {
  position: absolute;
  content: "";
  display: inline-block;
}

.checkbox label::before {
  height: 18px;
  width: 18px;
  border-radius: 2px;
  border: 2px solid #707070;
  left: 0px;
  flex: 0 0 26px;
  top: 3px;
} 

.checkbox label::after {
  height: 7px;
  width: 12px;
  border-left: 2px solid #707070;
  border-bottom: 2px solid #707070;
  transform: rotate(-45deg);
  left: 4px;
  top: 7px;
}

.checkbox input[type="checkbox"] + label::after {
  content: none;
}

.checkbox input[type="checkbox"]:checked + label::after {
  content: "";
}

