body {
  margin: 0px;
  font-family: "Roboto", sans-serif;
  min-width: 300px;
  overflow: hidden;
}

input {
  border: 0px;
  outline: none;
}

p {
  margin: 10px 0px 10px 0px;
}

.content_container {
  padding: 0px 10px 0px 10px;
  overflow: auto;
  height: calc(100vh - 50px);
}

.content__ {
  margin: 25px auto;
  max-width: 768px;
}

.title_container {
  text-align: center;
}

.title_container p {
  color: #484848;
  font-size: 20px;
}

.test-name_container {
  margin: 0px 0px 25px 0px;
  display: flex;
}

.text-input {
  flex: 1 1;
  border-bottom: 1px solid #4E4E4E;
  font-size: 16px;
  padding-bottom: 5px;
  color: #484848;
}

.text-input:focus {
  border-bottom: 2px solid #4185C9;
  margin-bottom: -1px;
}

.text-input::-webkit-input-placeholder {
  color: #989898;
}

.text-input:-ms-input-placeholder {
  color: #989898;
}

.text-input::-ms-input-placeholder {
  color: #989898;
}

.text-input::placeholder {
  color: #989898;
}

.add-question-button_container {
  display: flex;
